import LogicUtil from "../contents/create/factory/util/logicUtil";
import { Store } from "../redux/store/store";
import DataUtil from "./dataUtil";

namespace LocalStorageUtil {
    export const DEFAULT_FILE_NAME_PREFIX = 'project_';

    export const KEY_LOGIN_HASH = 'login-hash';
    // export const KEY_SCENARIO_NAMES = 'scenario-names';
    export const KEY_SCENARIO_SEQ = 'scenario-seq';
    export const KEY_SCENARIO_PREFIX = 'scenario-data-';
    export const KEY_DISP_PRESET = 'display-put-preset';
    export const KEY_PROP_PUT_INTERVAL = 'prop-put-interval';

    export const load = (store: Store, update: () => void) => {

        // ログイン
        if (store.system.user == null) {
            const userHash = localStorage.getItem(KEY_LOGIN_HASH);
            if (userHash != null) {
                const userSeq = DataUtil.getDecryptionedUserSeq(userHash);
                if (userSeq !== -1) {
                    (async () => {
                        const results = await DataUtil.findUserInfoSeq(userSeq);
                        if (results.length > 0) {
                            store.system.user = {
                                seq: results[0].seq,
                                id: results[0].id,
                                password: results[0].password,
                                email: results[0].email,
                            };

                            // developの場合、クラウドのライブラリを読み込む
                            if (!store.system.isSPMode) {
                                const resPrjList = await DataUtil.findUserLibdvList(results[0].seq);
                                store.cloud = {
                                    projects: resPrjList.map(res => ({
                                        source: res.source,
                                        name: res.name,
                                        seq: res.seq,
                                        lastmod: res.lastmod
                                    }))
                                };
                            }
                            // update();
                        }
                    })();
                } else {
                    localStorage.removeItem(KEY_LOGIN_HASH);
                }
            }
        }

        // シナリオファイルのロード
        const scenarioSeq = localStorage.getItem(KEY_SCENARIO_SEQ);
        if (scenarioSeq == null) {
            localStorage.setItem(KEY_SCENARIO_SEQ, '0');
        } else {
            const seq = Number(scenarioSeq);
            store.local.maxSeq = seq;
            for (let i = 0; i <= seq; i++) {
                const source = localStorage.getItem(KEY_SCENARIO_PREFIX + i.toString());
                if (source != null) {
                    store.local.projects.push(JSON.parse(source));
                }
            }
        }

        // シード配置のインターバル初期値
        let putInterval = localStorage.getItem(KEY_PROP_PUT_INTERVAL);
        if(putInterval == null) {
            putInterval = '50';
            localStorage.setItem(KEY_PROP_PUT_INTERVAL, putInterval);
        }
        console.log(putInterval);
        store.local.putInterval = Number(putInterval);

        // シード配置のインターバル初期値
        let isDispPreset = localStorage.getItem(KEY_DISP_PRESET);
        if(isDispPreset == null) {
            isDispPreset = '0';
            localStorage.setItem(KEY_DISP_PRESET, isDispPreset);
        }
        store.local.isDispPreset = isDispPreset === '0';
    }
}

export default LocalStorageUtil;