import styled from "styled-components";
import PublishFrame from "../../../contents/home/publishFrame";
import FormUtil from "../../../utils/form/formUtiil";
import { useContext, useEffect, useMemo, useState, useRef } from "react";
import SPManageFrame from "./sPManageFrame";
import SPEditStoryFrame from "./edit/sPEditStoryFrame";
import { GlobalContext } from "../../../contents/entry/systemEntry";

const SPCreateChannel = () => {
    const { store, setStore } = useContext(GlobalContext);

    const [isEdit, setEdit] = useState<boolean>(false);
    const hasData = store.libraryData != null;

    return (<>
        <_SwitchWrap>
            <FormUtil.SwitchTwoFace
                label1="Manage"
                label2="Edit"
                rate1={50}
                rate2={50}
                isUse={isEdit}
                width={550}
                isEditable={hasData}
                isLarge={true}
                callback={() => {
                    setEdit(!isEdit);
                }}
            />
        </_SwitchWrap>
        <_MainDiv>
            {!isEdit ? <SPManageFrame isEdit={isEdit} setEdit={setEdit} /> : <SPEditStoryFrame />}
        </_MainDiv>
    </>);
}

export default SPCreateChannel;

const _SwitchWrap = styled.div`
  display: inline-block;
  position: relative;
  /* background-color: #9fc99e; */
  width: 100%;
  text-align: center;
  margin-bottom: 6px;
`;

const _MainDiv = styled.div`
  display: inline-block;
  position: relative;
  /* background-color: #9fc99e; */
  width: 100%;
  height: calc(100% - 60px);
`;