import StoreLocalStrage from "../../../../redux/store/storeLocalStrage";
import DataUtil from "../../../../utils/dataUtil";

namespace ShareCloudUtil {

    export const uploadShareScenario = async (project: StoreLocalStrage.Project) => {

        let cloudSeq = project.cloudSeq;
        let isExists = false;

        if (cloudSeq === -1) {
            cloudSeq = await getNextSeq();
        } else {
            isExists = await isExistsAlreadySameSeq(cloudSeq);
        }

        const sourcePlain = JSON.stringify(project.scenarioData);
        const sourceZip = DataUtil.gZip(sourcePlain);
        const libhash = DataUtil.getSHA256Hash(JSON.stringify(project.libraryData));

        // 既に登録されている場合は上書きする
        if (isExists) {
            await overwriteSnrshRecord(cloudSeq, project.name, sourceZip)
        } else {
            await insertSnrshRecord(cloudSeq, project.name, libhash, sourceZip);

            const existsHash = await isExistsSameHash(libhash);
            // ハッシュがなければ登録する
            if (!existsHash) {
                const libSourcePlain = JSON.stringify(project.libraryData);
                const libSourceZip = DataUtil.gZip(libSourcePlain);
                const owner = -1;
                await insertLibhsRecord(libhash, project.libName, libSourceZip, owner);
            }
        }

        console.log('cloudSeq: ' + cloudSeq);
        return cloudSeq;
    }

    export const deleteShareScenario = async (cloudSeq: number) => {

        await deleteSnrshRecord(cloudSeq);

        // 同じライブラリのハッシュを使っているシナリオがあるか
        // const existsUseHash = await isExistsSameHash(libhash);
        // // ハッシュがなければ登録する
        // if (!existsHash) {
        //     const libSource = JSON.stringify(project.libraryData);
        //     const owner = -1;
        //     await insertlibhsRecord(libhash, project.libName, libSource, owner);
        // }
    }

    const getNextSeq = async () => {
        const response = await DataUtil.sendQueryRequestToAPI('select', `SELECT max(seq) as m FROM snrshtbl`);
        const results = await response.json();
        const records = results as { m: number }[];
        return records.length === 0 ? 0 : (records[0].m + 1);
    };

    const insertSnrshRecord = async (seq: number, name: string, libhash: string, source: string) => {
        DataUtil.sendQueryRequestToAPI('update',
            `INSERT INTO snrshtbl(seq, name, libhash, source, lastmod)
            VALUES(${seq}, '${name}', '${libhash}', '${source}', datetime ('now'))`
        );
    }

    const overwriteSnrshRecord = async (seq: number, name: string, source: string) => {
        DataUtil.sendQueryRequestToAPI('update',
            `UPDATE snrshtbl SET name = '${name}', source = '${source}', lastmod = datetime ('now')
            WHERE seq = ${seq}`
        );
    }

    const isExistsAlreadySameSeq = async (cloudSeq: number) => {
        const response = await DataUtil.sendQueryRequestToAPI('select', `SELECT count(*) as c FROM snrshtbl where seq = ${cloudSeq}`);
        const results = await response.json();
        const records = results as { c: number }[];
        return records[0].c > 0;
    };

    const deleteSnrshRecord = async (seq: number) => {
        DataUtil.sendQueryRequestToAPI('update', `DELETE FROM snrshtbl where seq = ${seq}`);
    }

    const isExistsSameHash = async (hash: string) => {
        const response = await DataUtil.sendQueryRequestToAPI('select', `SELECT count(*) as c FROM libhstbl where hash = '${hash}'`);
        const results = await response.json();
        const records = results as { c: number }[];
        return records[0].c > 0;
    };

    const insertLibhsRecord = async (hash: string, name: string, source: string, owner: number) => {
        DataUtil.sendQueryRequestToAPI('update',
            `INSERT INTO libhstbl(hash, name, source, owner)
            VALUES('${hash}', '${name}', '${source}', ${owner})`
        );
    }
}

export default ShareCloudUtil;