import { createContext, useCallback, useEffect, useReducer, useState } from 'react';
import { initialStore, Store } from '../../redux/store/store';
import { HashRouter, Redirect, Route, Switch, useLocation } from "react-router-dom";
import MobileRoot from '../../sp/mobileRoot';
import PCRoot from './pCRoot';
import SPRoot from '../../sp/sPRoot';
import { Dispatcher } from '../../redux/dispatcher/dispatcher';
import DataUtil from '../../utils/dataUtil';
import SystemConst from '../../utils/systemConst';
import LocalStorageUtil from '../../utils/localStorageUtil';

type GlobalContextType = {
    store: Store;
    setStore: React.Dispatch<React.SetStateAction<Store>>;
    dispatcher: Dispatcher;
}

export const GlobalContext = createContext({} as GlobalContextType);

const SystemEntry = () => {

    // const { store, setStore } = useContext(GlobalContext);

    return (
        <HashRouter basename={process.env.PUBLIC_URL}>
            <RouterManage />
        </HashRouter>
    );
}

export default SystemEntry;

const RouterManage = () => {

    const [store, setStore] = useState(initialStore);
    const dispatcher = new Dispatcher(store, setStore);

    const query = new URLSearchParams(useLocation().search);
    const str = query.get('v') as string;

    useEffect(() => {
        // ローカルストレージから情報取得
        LocalStorageUtil.load(store, dispatcher.updateStore);
        dispatcher.updateStore();
    }, []);

    return (
        <GlobalContext.Provider value={{ store, setStore, dispatcher }}>
            <Switch>
                <Route path="/" exact>
                    {/* <PCRoot /> */}
                    {!SystemConst.IS_SP ? <PCRoot /> : <SPRoot />}
                </Route>
                {/* <Route path="/sp" exact >
                    <SPRoot />
                </Route> */}
                {/* <Route path="/library" exact >
                    <MobileRoot hashKey={str} />
                </Route> */}
                <Route path="/share" exact >
                    <SPRoot hashKey={str} />
                </Route>
            </Switch>
        </GlobalContext.Provider>
    );

}