
import LogicUtil from "../../../../contents/create/factory/util/logicUtil";
import libraries from "../../../../resource/library/library.json";
import DataUtil from "../../../../utils/dataUtil";

namespace LibraryLoader {

    export type LibInfo = {
        name: string;
        version: string;
        outline: string;
        rootData: LogicUtil.WrapElement;
    }

    export const getOfficialList = (): LibInfo[] => {
        return libraries.map(lib => {
            return {
                name: lib.name,
                version: lib.version,
                outline: lib.outline,
                rootData: JSON.parse(DataUtil.unZip(lib.source.toString())) as LogicUtil.WrapElement
            }
        });
    }

    export const getExtendList = () => {
        const list: LibInfo[] = [];


        return list;
    }
}

export default LibraryLoader;