import styled from "styled-components";
import PublishFrame from "../../contents/home/publishFrame";
import FormUtil from "../../utils/form/formUtiil";
import { useContext, useEffect, useMemo, useState, useRef } from "react";
import SPManageFrame from "./create/sPManageFrame";
import SPEditStoryFrame from "./create/edit/sPEditStoryFrame";
import { GlobalContext } from "../../contents/entry/systemEntry";
import LocalStorageUtil from "../../utils/localStorageUtil";
import { MdOutlineCleaningServices } from "react-icons/md";
import ConfirmDialog from "../../contents/dialog/confirmDialog";
import ValidateUtil from "../../utils/form/validateUtil";

const SPPropertyChannel = () => {
  const { store, dispatcher } = useContext(GlobalContext);
  const [interval, setInterval] = useState<FormUtil.CheckableValue>({ value: "", errors: [] });
  const [isDispPreset, setDispPreset] = useState<boolean>(false);

  useEffect(() => {
    if (store.system.channelIndex == 3) {
      reload();
      dispatcher.updateStore();
    }
  }, [store.system.channelIndex]);

  const reload = () => {
    interval.value = localStorage.getItem(LocalStorageUtil.KEY_PROP_PUT_INTERVAL) as string;
    setInterval({ ...interval });
    setDispPreset(localStorage.getItem(LocalStorageUtil.KEY_DISP_PRESET) === '0');

    store.local.putInterval = Number(interval.value);
    console.log(store.local.putInterval);
    store.local.isDispPreset = isDispPreset;
  }

  return (<>
    <FormUtil.BorderPanel
      title="Properties"
      height={155}
      innerJsx={<>
        <FormUtil.FormRecord
          titleLabel="Interval"
          jsx={
            <FormUtil.TextField
              width={100}
              isNumber
              checkable={interval}
              setCheckable={setInterval}
              validates={[
                {
                  checker: (value) => ValidateUtil.checkRequired(value),
                  errorType: "required"
                },
                {
                  checker: (value) => ValidateUtil.checkNumberRange(value, 0, 100),
                  errorType: "value"
                }
              ]}
            />
          }
        />
        <FormUtil.FormRecord
          titleLabel="Preset"
          jsx={
            <FormUtil.SwitchTwoFace
              label1="Off"
              label2="On"
              rate1={50}
              rate2={50}
              width={220}
              isUse={isDispPreset}
              callback={() => {
                setDispPreset(!isDispPreset);
              }}
            />
          }
        />
        <FormUtil.ButtonRecord
          align="left"
          buttons={[
            {
              label: 'Save',
              width: 200,
              icon: <MdOutlineCleaningServices />,
              callback: () => {
                store.local.putInterval = Number(interval.value);
                store.local.isDispPreset = isDispPreset;
                localStorage.setItem(LocalStorageUtil.KEY_DISP_PRESET, store.local.isDispPreset ? '0' : '1');
                localStorage.setItem(LocalStorageUtil.KEY_PROP_PUT_INTERVAL, interval.value);

                // 更新完了のダイアログを表示
                store.system.dialog = <ConfirmDialog.Component
                  message="Save succesful!"
                />;
                dispatcher.updateStore();
              }
            },
            {
              label: 'Load',
              width: 200,
              icon: <MdOutlineCleaningServices />,
              callback: () => {
                reload();
                // 読み込み完了のダイアログを表示
                store.system.dialog = <ConfirmDialog.Component
                  message="Load succesful!"
                />;
                dispatcher.updateStore();
              }
            }
          ]} />
      </>}
    />
    <FormUtil.ButtonRecord
      align="left"
      buttons={[
        {
          label: 'Local storage all clear',
          width: 300,
          icon: <MdOutlineCleaningServices />,
          callback: () => {
            localStorage.removeItem(LocalStorageUtil.KEY_LOGIN_HASH);
            localStorage.removeItem(LocalStorageUtil.KEY_PROP_PUT_INTERVAL);
            const seqMax = Number(localStorage.getItem(LocalStorageUtil.KEY_SCENARIO_SEQ));
            for (let i = 0; i <= seqMax; i++) {
              localStorage.removeItem(LocalStorageUtil.KEY_SCENARIO_PREFIX + i.toString());
            }
            localStorage.removeItem(LocalStorageUtil.KEY_SCENARIO_SEQ);

            const local = store.local;
            local.maxSeq = 0;
            local.projects = [];
            store.editing.localSeq = -1;

            // 削除完了のダイアログを表示
            store.system.dialog = <ConfirmDialog.Component
              message="All clear succesful!"
            />;
            dispatcher.updateStore();
          }
        }
      ]} />
  </>);
}

export default SPPropertyChannel;

const _MainDiv = styled.div`
  display: inline-block;
  position: relative;
  /* background-color: #9fc99e; */
  width: 100%;
  height: calc(100% - 40px);
`;